<template>
  <div class="about">
  
    <div class="label_sort">並び替え</div>
    <span v-if="sort_sw == 1">
      <div v-on:click="sort_Data2()" class="label_tab1">新着順</div>
    </span>
    <span v-else>
      <div v-on:click="sort_Data2()" class="label_tab2">新着順</div>
    </span>

    <span v-if="sort_sw == 3">
      <div v-on:click="sort_Data3()" class="label_tab1">ジャンル別</div>
    </span>
    <span v-else>
      <div v-on:click="sort_Data3()" class="label_tab2">ジャンル別</div>
    </span>

    <span v-if="sort_sw == 4">
      <div v-on:click="sort_Data4()" class="label_tab1">エリア別</div>
    </span>
    <span v-else>
      <div v-on:click="sort_Data4()" class="label_tab2">エリア別</div>
    </span>

    <span v-if="sort_sw == 5">
      <div v-on:click="sort_Random()" class="label_tab1">ランダム</div>
    </span>
    <span v-else>
      <div v-on:click="sort_Random()" class="label_tab2">ランダム</div>
    </span>
    <br>

    <svg
      v-bind:width="svg_w"
      v-bind:height="svg_h"
     >

      <!----- background/ ----->
      <g v-for="i in 4"  :key="`bg_cnt_${i}`">
        <image
        v-bind="{'xlink:href' : require('../assets/img/bg' + i + '.png')}"
        v-bind:x="(bg_x[i - 1])"
        v-bind:y="(bg_y[i - 1])"
        v-bind:transform="'rotate(' + (bg_r[i - 1]) + ')'"
        opacity="0.3"
        v-bind:width="bg_s[i - 1]"
        />

      </g>
      <!----- /background ----->


      <g v-for="i in data_cnt"  :key="`data_cnt_${i}`">

        <image
        v-bind="{'xlink:href' : require('../assets/img/' + i + '.jpg')}"
        v-bind:x="(gsap_x[i - 1])"
        v-bind:y="(gsap_y[i - 1])"
        v-bind:width="svg_w / 4 - 20"
        v-bind:transform="'rotate(' + (gsap_r[i - 1]) + ')'"
        v-on:click="view_Modal(Data1[i - 1])"
        v-on:mouseover="in_Modal(Data1[i - 1])"
        v-on:mouseleave="out_Modal()"
        />

        <rect
        v-bind:x="(gsap_x[i - 1])"
        v-bind:y="(gsap_y[i - 1])"
        v-bind:width="svg_w / 4 - 20"
        v-bind:height="40"
        fill="#000000"
        fill-opacity="0.7"
        v-bind:transform="'rotate(' + (gsap_r[i - 1]) + ')'"
        v-on:click="view_Modal(Data1[i - 1])"
        v-on:mouseover="in_Modal(Data1[i - 1])"
        v-on:mouseleave="out_Modal()"
        >
        </rect>

        <text
        fill="#ffffff"
        font-size="18"
        font-weight="bold"
        v-bind:x="(gsap_x[i - 1]) + 10"
        v-bind:y="(gsap_y[i - 1]) + 26"
        style="user-select: none;"
        v-bind:transform="'rotate(' + (gsap_r[i - 1]) + ')'"
        v-on:click="view_Modal(Data1[i - 1])"
        v-on:mouseover="in_Modal(Data1[i - 1])"
        v-on:mouseleave="out_Modal()"
        >
        {{ Data1[i - 1]["name"] }}
        </text>


        <rect
        v-bind:x="(gsap_x[i - 1])"
        v-bind:y="(gsap_y[i - 1]) + 190"
        v-bind:width="svg_w / 4 - 20"
        v-bind:height="30"
        fill="#000000"
        fill-opacity="0.5"
        v-bind:transform="'rotate(' + (gsap_r[i - 1]) + ')'"
        v-on:click="view_Modal(Data1[i - 1])"
        v-on:mouseover="in_Modal(Data1[i - 1])"
        v-on:mouseleave="out_Modal()"
        >
        </rect>


        <g v-if="sort_sw == 1">
          <text
          fill="#ffffff"
          font-size="14"
          font-weight="bold"
          v-bind:x="(gsap_x[i - 1]) + 320"
          v-bind:y="(gsap_y[i - 1]) + 210"
          style="user-select: none;"
          v-bind:transform="'rotate(' + (gsap_r[i - 1]) + ')'"
          text-anchor="end"
          v-on:click="view_Modal(Data1[i - 1])"
          v-on:mouseover="in_Modal(Data1[i - 1])"
          v-on:mouseleave="out_Modal()"
          >
            {{ Data1[i - 1]["date"] }}
          </text>
        </g>

        <g v-if="sort_sw == 3">
          <text
          fill="#ffffff"
          font-size="14"
          font-weight="bold"
          v-bind:x="(gsap_x[i - 1]) + 320"
          v-bind:y="(gsap_y[i - 1]) + 210"
          style="user-select: none;"
          v-bind:transform="'rotate(' + (gsap_r[i - 1]) + ')'"
          text-anchor="end"
          v-on:click="view_Modal(Data1[i - 1])"
          v-on:mouseover="in_Modal(Data1[i - 1])"
          v-on:mouseleave="out_Modal()"
          >
            {{ Data1[i - 1]["group"] }}
          </text>
        </g>

        <g v-if="sort_sw == 4">
          <text
          fill="#ffffff"
          font-size="14"
          font-weight="bold"
          v-bind:x="(gsap_x[i - 1]) + 320"
          v-bind:y="(gsap_y[i - 1]) + 210"
          style="user-select: none;"
          v-bind:transform="'rotate(' + (gsap_r[i - 1]) + ')'"
          text-anchor="end"
          v-on:click="view_Modal(Data1[i - 1])"
          v-on:mouseover="in_Modal(Data1[i - 1])"
          v-on:mouseleave="out_Modal()"
          >
            {{ Data1[i - 1]["address"] }}
          </text>
        </g>

        <g v-if="modal_focus == i">
          <rect
          v-bind:x="(gsap_x[i - 1])"
          v-bind:y="(gsap_y[i - 1])"
          v-bind:width="svg_w / 4 - 20 - 4"
          v-bind:height="220 - 4"
          v-bind:transform="'rotate(' + (gsap_r[i - 1]) + ')'"
          fill="none"
          stroke="#3fa756"
          stroke-width="8"
          />
        </g>

      </g>

    </svg>


    <!----- modal/ ----->
    <div v-if="modal_mode == true">
      <div v-on:click="switch_Modal(false)" class="modal_bg">
      </div>

      <div class="modal">

        <div class="modal_close">
          <button v-on:click="switch_Modal(false)" class="modal_btn">×</button>
        </div>

        <div class="modal_row">
          <div class="modal_head">
            <div class="modal_label">
            {{ modal_data.name }}
            </div>
            {{ modal_data.date }}<br>
            {{ modal_data.group }}<br>
            {{ modal_data.address }}<br>
          </div>
          <div class="modal_data">

            <span v-for="i in modal_data.photo"  :key="`photo_cnt_${i}`">

              <img
              v-bind="{'src' : require('../assets/img/' + modal_data.id + '_' + i + '.jpg')}"
              class="modal_img"
              >

            </span>

          </div>
        </div>

      </div>
    </div>
    <!----- /modal ----->

  </div>
</template>
<script>
  import moment from "moment"
  import gsap from 'gsap'

  import Data1 from "../assets/data/afris_s1.json"
  import Data2 from "../assets/data/afris_s2.json"
  import Data3 from "../assets/data/afris_s3.json"
  import Data4 from "../assets/data/afris_s4.json"

  export default {
    name: 'Gsap',
    data() {
      return {
        now: moment().format('YYYY-MM-DD'),
        svg_w:1400,
        //svg_h:2800,
        svg_h:2680,
        gsap_speed: 3,
        //gsap_speed: 5,
        //gsap_speed: 10,

        //data_cnt: 500,
        //data_cnt: 1000,
        //data_cnt: 100,
        //data_cnt: 15,
        //data_cnt: 42,
        data_cnt: 0,
        Data1: Data1,
        Data2: Data2,
        Data3: Data3,
        sort_sw: 1,

        gsap_x: [],
        gsap_y: [],
        gsap_r: [],

        bg_speed: 3,
        bg_x: [],
        bg_y: [],
        //bg_r: [],
        bg_r: [0, 0, 0, 0],
        bg_s: [],

        // modal
        modal_mode:false,
        modal_data:'',
        modal_focus:0,

      }
    },
    mounted() {
      // 初期化

      this.data_cnt = Object.keys(Data1).length

      for (let i = 0; i < this.data_cnt; i++) {
        this.gsap_x[i] = Math.round( Math.random() * (this.svg_w - 400) + 200 )
        this.gsap_y[i] = Math.round( Math.random() * (this.svg_h - 400) + 200 )
        this.gsap_r[i] = Math.round( Math.random() * 100 ) - 50
        //this.gsap_x[i] = i * 400
        //this.gsap_y[i] = Math.floor(i / 5) * 200
      }
      //this.run_GSAP()
      //this.sort_Data1()
      this.sort_Data2()


      // 定期更新
      for (let i = 0; i < 4; i++) {
        this.bg_x[i] = Math.round( Math.random() * (this.svg_w - 400))
        this.bg_y[i] = Math.round( Math.random() * (this.svg_h - 400))
        this.bg_r[i] = Math.round( Math.random() * 45)
        this.bg_s[i] = Math.round( Math.random() * 2000) + 200
      }

      //this.auto_Update()
      this.bg_Update()

    },
    watch: {
    },
    methods: {

      sort_Data1() {
        this.sort_sw = 1

        let x_list = []
        let y_list = []
        let r_list = []

        let cnt = Object.keys(Data1).length

        for (let i = 0; i < cnt; i++) {
          let i2 = Number(Data1[i]["id"]) - 1

          x_list[i2] = (i % 4) * (this.svg_w / 4) + 20
          y_list[i2] = Math.floor(i / 4) * 240 + 20
          r_list[i2] = 0
        }

        gsap.to(this.gsap_x, this.gsap_speed, x_list)
        gsap.to(this.gsap_y, this.gsap_speed, y_list)
        gsap.to(this.gsap_r, this.gsap_speed, r_list)
      },

      sort_Data2() {
        this.sort_sw = 2

        let x_list = []
        let y_list = []
        let r_list = []

        let cnt = Object.keys(Data2).length

        for (let i = 0; i < cnt; i++) {
          let i2 = Number(Data2[i]["id"]) - 1

          x_list[i2] = (i % 4) * (this.svg_w / 4) + 20
          y_list[i2] = Math.floor(i / 4) * 240 + 20
          r_list[i2] = 0
        }

        gsap.to(this.gsap_x, this.gsap_speed, x_list)
        gsap.to(this.gsap_y, this.gsap_speed, y_list)
        gsap.to(this.gsap_r, this.gsap_speed, r_list)
      },

      sort_Data3() {
        this.sort_sw = 3

        let x_list = []
        let y_list = []
        let r_list = []

        let cnt = Object.keys(Data3).length

        for (let i = 0; i < cnt; i++) {
          let i2 = Number(Data3[i]["id"]) - 1

          x_list[i2] = (i % 4) * (this.svg_w / 4) + 20
          y_list[i2] = Math.floor(i / 4) * 240 + 20
          r_list[i2] = 0
        }

        gsap.to(this.gsap_x, this.gsap_speed, x_list)
        gsap.to(this.gsap_y, this.gsap_speed, y_list)
        gsap.to(this.gsap_r, this.gsap_speed, r_list)
      },

      sort_Data4() {
        this.sort_sw = 4

        let x_list = []
        let y_list = []
        let r_list = []

        let cnt = Object.keys(Data4).length

        for (let i = 0; i < cnt; i++) {
          let i2 = Number(Data4[i]["id"]) - 1

          x_list[i2] = (i % 4) * (this.svg_w / 4) + 20
          y_list[i2] = Math.floor(i / 4) * 240 + 20
          r_list[i2] = 0
        }

        gsap.to(this.gsap_x, this.gsap_speed, x_list)
        gsap.to(this.gsap_y, this.gsap_speed, y_list)
        gsap.to(this.gsap_r, this.gsap_speed, r_list)
      },


      sort_Random() {
        this.sort_sw = 5

        let x_list = []
        let y_list = []
        let r_list = []

        for (let i = 0; i < this.data_cnt; i++) {
          x_list[i] = Math.round( Math.random() * 600) - 400 + this.svg_w / 2
          y_list[i] = Math.round( Math.random() * 2000) - 1000 + this.svg_h / 2
          //r_list[i] = Math.round( Math.random() * 100 ) - 50
          r_list[i] = Math.round( Math.random() * 40 ) - 20
        }

        gsap.to(this.gsap_x, this.gsap_speed, x_list)
        gsap.to(this.gsap_y, this.gsap_speed, y_list)
        gsap.to(this.gsap_r, this.gsap_speed, r_list)
      },


      bg_Update() {
        setInterval(function () {
          //this.run_GSAP()

          let x_list = []
          let y_list = []
          let r_list = []
          let s_list = []

          for (let i = 0; i < 4; i++) {
            x_list[i] = Math.round( Math.random() * (this.svg_w - 400))
            y_list[i] = Math.round( Math.random() * (this.svg_h - 400))
            r_list[i] = Math.round( Math.random() * 45)
            s_list[i] = Math.round( Math.random() * 2000) + 200
          }

          gsap.to(this.bg_x, this.bg_speed, x_list)
          gsap.to(this.bg_y, this.bg_speed, y_list)
          gsap.to(this.bg_r, this.bg_speed, r_list)
          gsap.to(this.bg_s, this.bg_speed, s_list)

        }.bind(this), 1000 * this.bg_speed)
      },

      switch_Modal(v) {
        this.modal_mode = v
      },
      view_Modal(v) {
        this.switch_Modal(true)
        this.modal_data = v
      },

      in_Modal(v) {
        //this.switch_Modal(true)
        //this.modal_data = v
        //console.log(v)
        this.modal_focus = v["id"]
      },
      out_Modal() {
        //this.switch_Modal(true)
        //this.modal_data = v
        //console.log(v)
        this.modal_focus = 0
      },


    },


  }

</script>